<script setup lang="ts">
import { useQuestionsModel } from '@/entities/questions/model/questions.model';
import { Paragraph } from '@/shared/ui/text/paragraph';
import { Icon } from '@/shared/ui/icons';
import { computed } from 'vue';
import { useRoute } from 'vue-router';

interface Question {
  id: string;
  question: string;
  answer: string;
  slug: string; 
}

const { questions, activeQuestionIndex, toggleQuestion } = useQuestionsModel();
const route = useRoute();

const computedQuestions = computed<Question[]>(() => {
  return questions.value && questions.value.length > 0
    ? questions.value.filter((questionItem: Question) => questionItem.slug === route.path)
    : [];
});
</script>

<template>
  <ul class="questions__list" v-if="computedQuestions.length">
    <li class="item" v-for="(questionItem, index) in computedQuestions" :key="questionItem.id"
        :class="{ item_active: activeQuestionIndex === index }" @click="toggleQuestion(index)">
      <div class="item__question">
        <Paragraph tagName="p" size="m" color="dark">
          <Paragraph tagName="span" size="m" color="dark-gray">0{{ index + 1 }}</Paragraph>
          {{ questionItem.question }}
        </Paragraph>
        <Icon :rotate="activeQuestionIndex === index ? 180 : 0" type="arrowDown" color="dark" class="rotate-icon"></Icon>
      </div>
      <Paragraph v-if="activeQuestionIndex === index" tagName="p" size="xs" color="dark-gray" class="item__answer">
        {{ questionItem.answer }}
      </Paragraph>
    </li>
  </ul>
  <div v-else>
    <p>No questions available</p>
  </div>
</template>

<style lang="scss" scoped>
@import './style.scss';
</style>
