import axios from "axios";

interface AuthState {
    isAuthenticated: boolean;
    token: string | null;
}

export const authModule = {
    state: (): AuthState => ({
        isAuthenticated: !!localStorage.getItem('token'),
        token: localStorage.getItem('token') || null,
    }),
    mutations: {
        setAuth(state: AuthState, status: boolean) {
            state.isAuthenticated = status;
            if (!status) {
                localStorage.removeItem('token');
                state.token = null;
            } else {
                console.log('Аутентификация успешна');
            }
        },
        setToken(state: AuthState, token: string) {
            state.token = token;
            localStorage.setItem('token', token);
            state.isAuthenticated = true;
        },
    },
    actions: {
        async login({ commit }: { commit: Function }, credentials: { login: string; password: string }) {
            const response = await axios.post('http://5.35.90.116:8000/login/', credentials);
            commit('setToken', response.data.access);
        },
        async logout({ commit }: { commit: Function }) {
            try {
                const token = localStorage.getItem('token');
                if (token) {
                    await axios.post('http://5.35.90.116:8000/logout/', {}, {
                        headers: {
                            Authorization: `Bearer ${token}`
                        }
                    });
                }
                commit('setAuth', false);
            } catch (err) {
                console.error('Ошибка при выходе:', err); 
            }
        },
        checkAuth({ commit }: { commit: Function }) {
            const token = localStorage.getItem('token');
            if (token) {
                commit('setAuth', true);
            } else {
                commit('setAuth', false);
            }
        },
    },
    getters: {
        isAuthenticated: (state: AuthState) => state.isAuthenticated,
        token: (state: AuthState) => state.token,
    },
};
