<script setup lang="ts">
import { Paragraph } from '@/shared/ui/text/paragraph';
import { RouterLink } from 'vue-router';
import { computed } from 'vue';
import { useRoute } from 'vue-router';
const route = useRoute()
const isHomePage = computed(() => route.path === '/')

interface Props {
    navItems: {
        id: number;
        name: string;
        link: string;
    }[]
}
const { navItems } = defineProps<Props>();

</script>
<template>
    <nav class="navigation" :class="{ 'navigation_dark-theme': isHomePage, '': !isHomePage }">
        <ul class="navigation__list">
            <li v-for="item in navItems" :key="item.id" class="item">
                <RouterLink :to="item.link" tagName="roter-link" class="item__link">
                    <Paragraph tag-name="span" size="xs" color="dark" class="item__text">
                        {{ item.name }}
                    </Paragraph>
                </RouterLink>
            </li>
        </ul>
    </nav>
</template>
<style scoped lang="scss">
@import './style.scss'
</style>