import axios from 'axios';
import { Results } from '@/entities/results-before-after/model/results.types'

interface ResultsState {
    ResultsData: Results[];
}

export const resultsModule = {
    namespaced: true,
    state: (): ResultsState => ({
        ResultsData: []
    }),
    mutations: {
        setResults(state: ResultsState, results: Results[]) {
            state.ResultsData = results;
        } 
    },
    actions: {
        async fetchResults({ commit }: { commit: Function }) {
            try {
                const response = await axios.get<Results[]>('http://5.35.90.116:8000/results');
                const results: Results[] = response.data;
                commit('setResults', results);
            } catch (error) {
                console.error('Ошибка при получении данных результатов:', error);
            }
        }
    }
};
