import axios, { AxiosError } from 'axios';

interface RegistrationState {
  registrationSuccess: boolean;
  registrationError: string | null;
}

export const registrationModule = {
  state: (): RegistrationState => ({
    registrationSuccess: false,
    registrationError: null,
  }),
  mutations: {
    setRegistrationSuccess(state: RegistrationState, status: boolean) {
      state.registrationSuccess = status;
    },
    setRegistrationError(state: RegistrationState, error: string | null) {
      state.registrationError = error;
    },
  },
  actions: {
    async register({ commit }: { commit: Function }, userData: {
      email: string;
      login: string;
      password: string;
      phone: string;
      first_name: string;
      last_name: string;
      middleName: string;
    }) {
      try {
        const response = await axios.post('http://5.35.90.116:8000/registration/', userData);
        console.log('Ответ сервера:', response.data); 
        commit('setRegistrationSuccess', true);
        commit('setRegistrationError', null);
      } catch (error: unknown) {
        if (axios.isAxiosError(error)) {
          const axiosError = error as AxiosError<any>;
          const errorMessage = axiosError.response?.data?.detail || 'Ошибка при регистрации';
          console.error('Ошибка Axios:', axiosError.response?.data); 
          commit('setRegistrationError', errorMessage);
        } else {
          console.error('Неизвестная ошибка:', error);
          commit('setRegistrationError', 'Неизвестная ошибка');
        }
        commit('setRegistrationSuccess', false);
      }
    },
  },
  getters: {
    registrationSuccess: (state: RegistrationState) => state.registrationSuccess,
    registrationError: (state: RegistrationState) => state.registrationError,
  },
};
