import axios from 'axios';
import { ActionContext } from 'vuex';
import { Questions } from '@/entities/questions/model/question.types'

interface QuestionsState {
    QuestionsData: Questions[];
}

export const questionsModule = {
    namespaced: true,
    state: (): QuestionsState => ({
        QuestionsData: []
    }),
    mutations: {
        setQuestions(state: QuestionsState, questions: Questions[]) {
            state.QuestionsData = questions;
        }
    },
    actions: {
        async fetchQuestions({ commit }: { commit: Function }) {
            try {
                const response = await axios.get<Questions[]>('http://5.35.90.116:8000/questions');
                const questions: Questions[] = response.data;
                commit('setQuestions', questions);
            } catch (error) {
                console.error('Ошибка при получении данных вопросов:', error);
            }
        }
    }
};
